import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import { ImageDataUrls } from 'algorand-qrcode-react'

const { logo } = ImageDataUrls()
const styles = (theme) => ({
    textField: {
        width: '100%'
      },

})
class SecondDeviceResults extends Component {
  constructor() {
    super()
    this.state = {}
    
  }


  render() {
    const { classes } = this.props
    
    const self = this

    return (
      <div>
         <h3>Second device results</h3>
         <TextField
            id='results'
            label='Results'
            variant='outlined'
            multiline
            rows={5}
            className={classes.textField}
            // onChange={}
            // value={}
            margin='normal'
            />
            
        </div> 
    )
  }
}
SecondDeviceResults.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,
}
export default withStyles(styles)(SecondDeviceResults)
