import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'
import { GithubPicker } from 'react-color'
import QRCode from 'algorand-qrcode'
import nextId from 'react-id-generator'
import { ImageDataUrls } from 'algorand-qrcode-react'
import AccountsSmartView from '../components/AccountsSmartView'

import Modal from '@material-ui/core/Modal'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogActions from '@material-ui/core/DialogActions'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import Link from '@material-ui/core/Link'
import FileCopy from '@material-ui/icons/FileCopyOutlined'
import QrCodelogo from '@material-ui/icons/DashboardOutlined'
import MyAlgo from '@randlabs/myalgo-connect'

//import CheckCircle from '@material-ui/icons/CheckCircle'
import { core } from '@algonaut/algo-validation-agent/dist/algo-validation-agent.cjs'

const { logo } = ImageDataUrls()
const styles = (theme) => ({
  root: {
    maxWidth: '60%',
    margin: 'auto',
    padding: theme.spacing(3),
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3),
    [theme.breakpoints.down('xs')]: {
      maxWidth: '95%'
    }
  },
  addressField: {
    minWidth: '90%',
    width: '100%',
    verticalAlign: 'middle'
  },
  walletContainer: {
    width: '100%',
    verticalAlign: 'middle',
    textAlign: 'center'
  },
  addressInput: {
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.6em'
    }
  },
  title: {
    margin: `${theme.spacing(3)}px 0 ${theme.spacing(2)}px`,
    color: '#333333',
    textAlign: 'center',
    fontSize: '1.2em',
    fontWeight: 'bold'
  },
  textField: {
    width: '100%'
  },
  formControl: {
    width: '90%'
  },
  colorSelect: {
    // width: '40%',
    marginRight: 50,
    [theme.breakpoints.down('xs')]: {
      marginRight: 20,
      marginBottom: 20
    }
  },
  generateBtn: {
    borderRadius: 3,
    textAligne: 'center',
    margin: 'auto',
    display: 'block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)',
    color: '#fff',
    '&:hover': {
      backgroundColor: '#fff',
      color: 'rgb(115, 175, 245)'
    }
  },
  qrcodeDiv: {
    borderbottom: '10px solid rgb(115, 175, 245)',
    border: '1px solid #333',
    margin: 'auto',
    display: 'block'
    // padding: 10
  },
  qrImage: {
    width: '256px',
    height: '256px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      width: '140px',
      height: '140px'
    }
  },
  assetUrl: {
    [theme.breakpoints.down('xs')]: {
      wordWrap: 'break-word'
    }
  },
  margin: {
    margin: theme.spacing(3)
  },
  textDiv: {
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)',
    margin: 'auto',
    display: 'block'
  },
  subTitle: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  uri: {
    padding: '10px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.7em',
      wordBreak: 'break-word'
    }
  },
  walletAddressInput: {
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.58em',
      paddingLeft: 3,
      paddingRight: 3
    }
  },
  noteBtnText: {
    fontSize: '0.8em',
    padding: '6px 5px',
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.6em'
    }
  },
  noteBtnRoot: {
    minWidth: 35,
    borderRadius: '11px',
    border: '5px solid white',
    backgroundColor: 'lightblue'
  },
  noteBtnRootActive: {
    borderRadius: '11px',
    border: '5px solid white',
    minWidth: 45,
    backgroundColor: 'lightgreen'
  },
  noteBtnsDiv: {
    width: '100%',
    textAlign: 'right',
    position: 'absolute',
    top: '10px',
    right: '0px'
  },
  chromPicker: {
    [theme.breakpoints.down('xs')]: {
      width: '100px'
    }
  },
  dialog: {
    wordWrap: 'break-word',
    textAlign: 'center'
  },
  dialogContent: {
    width: '100%',
    minWidth: '400px',
    textAlign: 'center',
    overflowY: "auto",
    height: "70vh",
    "&::-webkit-scrollbar": {
      width: "5px",
    },
    "&::-webkit-scrollbar-track": {
      boxShadow: "inset 0 0 5px grey",
      borderRadius: "10px",
    },
    "&::-webkit-scrollbar-thumb": {
      background: '#5c5c5c',
      borderRadius: "10px",
    },
    "&::-webkit-scrollbar-thumb:hover": {
      background: '#474646',
    },
    scrollbarColor: '#5c5c5c',
    scrollbarWidth: "thin",
  },
  dialogPaper:{
    overflowX: "auto",
    "&::-webkit-scrollbar": {
      width: "3px",
    },
    "&::-webkit-scrollbar-track": {
      boxShadow: "inset 0 0 5px grey",
      borderRadius: "10px",
    },
    "&::-webkit-scrollbar-thumb": {
      background: '#5c5c5c',
      borderRadius: "10px",
    },
    "&::-webkit-scrollbar-thumb:hover": {
      background: '#474646',
    },
    scrollbarColor: '#5c5c5c',
    scrollbarWidth: "thin",
  },
  modal: {
    width: '40%',
    minWidth: '300px',
    padding: theme.spacing(3),
    overflowY: 'auto',
    '&::-webkit-scrollbar': {
      width: '5px'
    },
    '&::-webkit-scrollbar-track': {
      boxShadow: 'inset 0 0 5px grey',
      borderRadius: '10px'
    },
    '&::-webkit-scrollbar-thumb': {
      background: 'gray',
      borderRadius: '10px'
    },
    '&::-webkit-scrollbar-thumb:hover': {
      background: 'gray'
    },
    scrollbarColor: 'gray',
    scrollbarWidth: 'thin'
  },
  card: {
    [theme.breakpoints.down('xs')]: {
      width: '100%'
    }
  }
})
class PaymentTransactionExample extends Component {
  constructor() {
    super()
    this.state = {
      wallet: 'LP6QRRBRDTDSP4HF7CSPWJV4AG4QWE437OYHGW7K5Y7DETKCSK5H3HCA7Q',
      label: 'Your amazing wallet name',
      note: '',
      xnote: false,
      html: false,
      amount: 0,    
      inverse: false,
      margin: 4,
      uri: 'algorand://',
      walletUri: 'algorand://',
      errorLevel: 'high',
      version: 'auto',
      dataURL: logo,
      walletDataURL: logo,
      showOptIn: false,
      isWalletQrModalOpen: false,     
      walletOK: true,
      isWalletOptInAsset: false,
      darkColor: '#000',
      lightColor: '#FFF',
      isNoteHex: false,
      isNoteBase64: false,
      isNoteJson: false,
      isNoteTxt: true,
      isFilePickerModalOpen: false
    }
    this.generateQRCode = this.generateQRCode.bind(this)
    this.generateWalletQRCode = this.generateWalletQRCode.bind(this)
    this.handleClose = this.handleClose.bind(this)
    this.myAlgoWallet = null

    this.generateAccountQRCode = this.generateAccountQRCode.bind(this)
    this.handleNoteChange = this.handleNoteChange.bind(this)
    this.myAlgoConnect = this.myAlgoConnect.bind(this)
    this.isBase64 = this.isBase64.bind(this)
    this.isHex = this.isHex.bind(this)
    this.isJson = this.isJson.bind(this)
  }

  generateQRCode() {
    const {
      wallet,
      label,
      note,
      xnote,
      html,
      amount,
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const color = { light: lightColor, dark: darkColor }
    const errorCorrectionLevel = errorLevel

    const opts = {
      wallet,
      label,
      note,
      xnote,
      html,
      amount,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    const self = this
    opts.mode = 'Auto'
    QRCode.toDataURL(null, opts)
      .then((res) => {
        self.setState({ dataURL: res[0], uri: res[1] })
      })
      .catch((err) => {
        console.error(err)
      })
  }
  componentDidMount() {
    
  }
  handleClose() {
    this.setState({
      isWalletQrModalOpen: false,   
    })
  }
 
  handleNoteChange(event) {
    let str = event.target.value
    if (str === '') {
      this.setState({
        isNoteHex: false,
        isNoteBase64: false,
        isNoteJson: false,
        isNoteTxt: true
      })
    } else if (this.isJson(str)) {
      this.setState({
        isNoteHex: false,
        isNoteBase64: false,
        isNoteJson: true,
        isNoteTxt: false
      })
    } else if (this.isBase64(str)) {
      this.setState({
        isNoteHex: false,
        isNoteBase64: true,
        isNoteJson: false,
        isNoteTxt: false
      })
    } else if (this.isHex(str)) {
      this.setState({
        isNoteHex: true,
        isNoteBase64: false,
        isNoteJson: false,
        isNoteTxt: false
      })
    }
    this.setState({ note: str })
  }






  generateWalletQRCode() {
    const {
      wallet,
      label,
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const errorCorrectionLevel = errorLevel
    const color = { light: lightColor, dark: darkColor }

    const opts = {
      wallet,
      label,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    const self = this
    opts.mode = 'Auto'
    QRCode.toDataURL(null, opts)
      .then((res) => {
        self.setState({ walletDataURL: res[0], walletUri: res[1] })
      })
      .catch((err) => {
        console.error(err)
      })
  }

  generateAccountQRCode(wallet) {
    const {
      
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const errorCorrectionLevel = errorLevel
    const color = { light: lightColor, dark: darkColor }

    const opts = {
      wallet,
      label: wallet,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    opts.mode = 'Auto'
    QRCode.toDataURL(null, opts)
      .then((res) => {
        return { walletDataURL: res[0], walletUri: res[1] }
      })
      .catch((err) => {
        console.error(err)
      })
  }

  async myAlgoConnect() {
    const self = this
    try {
      this.myAlgoWallet =
        this.myAlgoWallet === null ? new MyAlgo() : this.myAlgoWallet
      const accounts = await this.myAlgoWallet.connect()
      console.log(accounts[0])
      self.setState({ wallet: accounts[0].address })
    } catch (err) {
      console.error(err)
    }
  }

  isBase64(str) {
    let indexOfBaseContextStr = -1

    try {
      if (str.indexOf('base64,') > -1) {
        indexOfBaseContextStr = str.indexOf('base64,') + 7
        str = str.substring(indexOfBaseContextStr, str.length)
      }
      window.atob(str)
    } catch (e) {
      console.log(e)
      return false
    }
    return true
  }

  isJson(str) {
    try {
      JSON.parse(str)
    } catch (e) {
      console.log(e)
      return false
    }
    return true
  }

  isHex(str) {
    const re = /[0-9A-Fa-f]{6}/g
    try {
      re.test(str)
    } catch (e) {
      console.log(e)
      return false
    }
    return re.test(str)
  }

  render() {
    const { classes } = this.props
    const {
      wallet,
      isWalletQrModalOpen,
      
      walletOK,
   
      isNoteHex,
      isNoteBase64,
      isNoteJson,
      isNoteTxt,
      isFilePickerModalOpen
    } = this.state
    const versionOptionsArray = []
    versionOptionsArray.push({ value: 'auto', label: 'auto' })
    for (let i = 0; i < 41; i++) {
      versionOptionsArray.push({ value: `${i}`, label: `${i}` })
    }
    const self = this

    return (
      <div>
        <Dialog
          className={classes.dialog}
          open={isWalletQrModalOpen}
          onClose={this.handleClose}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle id='alert-dialog-title'>
            {'Algorand Wallet: '}
          </DialogTitle>
          <DialogContent>
            <DialogContentText id='alert-dialog-description'>
              {`${this.state.wallet}`}
            </DialogContentText>
            <img
              alt='Algorand QRCode'
              className={classes.qrImage}
              src={this.state.walletDataURL}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color='primary' autoFocus>
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <Modal
          open={isFilePickerModalOpen}
          onClose={() => {
            self.setState({ isWalletQrModalOpen: false })
          }}
          aria-labelledby='simple-modal-title'
          aria-describedby='simple-modal-description'
        >
          {<div>HELLO</div>}
        </Modal>
        <Paper className={classes.root} elevation={2}>
          <Typography type='title' className={classes.title}>
            Payment Transaction Example
          </Typography>
          <div className={classes.walletContainer}>
            <TextField
              id='wallet'
              label='Wallet address'
              variant='outlined'
              value={wallet}
              className={classes.addressField}
              margin='normal'
              onChange={(event) => {
                const wallet = event.target.value
                this.setState({
                  wallet: wallet,
                  walletOk: core.isAlgorandAddress(wallet)
                })
              }}
              InputProps={{
                classes: {
                  input: classes.addressInput
                },
                endAdornment: (
                  <>
                    <Tooltip title='Copy wallet address'>
                      <IconButton
                        onClick={() => {
                          var copyText = document.getElementById('wallet')
                          copyText.select()

                          document.execCommand('copy')
                          alert('Copied the wallet address: ' + copyText.value)
                        }}
                        edge='end'
                      >
                        <FileCopy />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title='Show qr code address'>
                      <IconButton
                        onClick={() => {
                          self.generateWalletQRCode()

                          self.setState({
                            isWalletQrModalOpen: true
                          })
                        }}
                        edge='end'
                      >
                        <QrCodelogo />
                      </IconButton>
                    </Tooltip>
                    <Tooltip
                      title={
                        walletOK
                          ? 'Address ' +
                            wallet.substring(0, 5) +
                            '...' +
                            'is OK! Click To get another from MyAlgo wallet'
                          : 'Get address from MyAlgo wallet'
                      }
                    >
                      <IconButton
                        id='wallet-connect-button'
                        onClick={this.myAlgoConnect}
                        edge='end'
                      >
                        {walletOK ? (
                          <img
                            width={32}
                            height={32}
                            style={{
                              backgroundColor: '#ddf4dd',
                              padding: 5,
                              borderRadius: 50
                            }}
                            src={logo}
                          />
                        ) : (
                          <img
                            width={32}
                            height={32}
                            style={{
                              padding: 5,
                              borderRadius: 50
                            }}
                            src={logo}
                          />
                        )}
                      </IconButton>
                    </Tooltip>
                  </>
                )
              }}
            />
          </div>
          <TextField
            id='label'
            label='Label'
            variant='outlined'
            className={classes.textField}
            onChange={(event) => {
              this.setState({ label: event.target.value })
            }}
            value={this.state.label}
            margin='normal'
          />
          <TextField
            id='amount'
            label='Amount'
            value={this.state.amount}
            variant='outlined'
            className={classes.textField}
            type='number'
            onChange={(event) => {
              this.setState({ amount: event.target.value })
            }}
            InputProps={{ inputProps: { min: 0, max: 100 } }}
            margin='normal'
          />
         
          <TextField
            id='note'
            label='Note'
            variant='outlined'
            multiline
            rows={5}
            className={classes.textField}
            onChange={this.handleNoteChange}
            value={this.state.note}
            margin='normal'
            InputProps={{
              endAdornment: (
                <div className={classes.noteBtnsDiv}>
                  <Button
                    //  onClick={this.handleClickTxt}
                    classes={{
                      text: classes.noteBtnText,
                      root: isNoteTxt
                        ? classes.noteBtnRootActive
                        : classes.noteBtnRoot
                    }}
                    href=''
                  >
                    TXT
                  </Button>
                  <Button
                    //  onClick={this.handleClickJson}
                    classes={{
                      text: classes.noteBtnText,
                      root: isNoteJson
                        ? classes.noteBtnRootActive
                        : classes.noteBtnRoot
                    }}
                    href=''
                  >
                    JSON
                  </Button>

                  <Button
                    //  onClick={this.handleClickBase}
                    classes={{
                      text: classes.noteBtnText,
                      root: isNoteBase64
                        ? classes.noteBtnRootActive
                        : classes.noteBtnRoot
                    }}
                    href=''
                  >
                    BASE64
                  </Button>
                  <Button
                    //  onClick={this.handleClickBase}
                    classes={{
                      text: classes.noteBtnText,
                      root: isNoteHex
                        ? classes.noteBtnRootActive
                        : classes.noteBtnRoot
                    }}
                    href=''
                  >
                    HEX
                  </Button>
                </div>
              )
            }}
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ xnote: event.target.value })
                }}
              />
            }
            id='xNote'
            label='XNote'
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ inverse: event.target.value })
                }}
              />
            }
            id='inverse'
            label='Inverse'
          />
          <FormControlLabel
            control={
              <Checkbox
                onChange={(event) => {
                  this.setState({ html: event.target.value })
                }}
              />
            }
            id='html'
            label='HTML Escaping'
          />
          <Grid container>
            <Grid item xs={4} sm={4} md={4}>
              <FormControl variant='outlined' className={classes.formControl}>
              <InputLabel id='version'>Version</InputLabel>
              <Select
                id='version'
                value={this.state.version}
                onChange={(event) => {
                  this.setState({ version: event.target.value })
                }}
                label='Version'
              >
                {versionOptionsArray.map((version) => {
                  return (
                    <MenuItem selected key={nextId()} value={version.value}>
                      {version.label}
                    </MenuItem>
                  )
                })}
              </Select>
            </FormControl>

            </Grid>
            <Grid item xs={4} sm={4} md={4}>
             <FormControl variant='outlined' className={classes.formControl}>
              <TextField
              id='margin'
              label='Margin'
              variant='outlined'
              className={classes.textField}
              type='number'
              InputProps={{ inputProps: { min: 4 } }}
              value={this.state.margin}
              onChange={(event) => {
                this.setState({ margin: event.target.value })
              }}
            />
            </FormControl>
            </Grid>
            <Grid item xs={4} sm={4} md={4}>
              <FormControl variant='outlined' className={classes.formControl}>
              <InputLabel id='version'>Error Correction</InputLabel>
              <Select
                id='errorLevel'
                value={this.state.errorLevel}
                // onChange={handleChange}
                label='Error Correction'
                onChange={(event) => {
                  this.setState({ errorLevel: event.target.value })
                }}
              >
                <MenuItem value='low'>Low</MenuItem>
                <MenuItem value='medium'>Medium</MenuItem>
                <MenuItem value='quartile'>Quartile</MenuItem>
                <MenuItem selected value='high'>
                  High
                </MenuItem>
              </Select>
            </FormControl>

            </Grid>
          </Grid>
          
          
         
          <div className={classes.margin} />
          <FormControl variant='outlined' className={classes.colorSelect}>
          Light Color
            <GithubPicker              
              name='Light color'
              id='lightColor'
              colors={['#ffffff', '#ffe0cc', '#ffffcc', '#e6ffe6', '#d6f5f5', '#cce6ff', '#e6e6ff', '#f9e6ff', '#EB9694', '#FAD0C3', '#FEF3BD', '#C1E1C5', '#BEDADC', '#C4DEF6', '#BED3F3', '#D4C4FB']}
              className={classes.chromPicker}
              defaultValue='#ffffff'
              color={this.state.lightColor}
              onChangeComplete={(color) => {
                this.setState({ lightColor: color.hex })
              }}
            />
          </FormControl>

          <FormControl variant='outlined' className={classes.colorSelect}>
          Dark Color
            <GithubPicker
              name='Dark color'
              id='darkColor'
              colors={['#000000','#1f004d', '#800000', '#663300', '#004d00', '#004d4d', '#003333', '#00334d','#B80000', '#DB3E00', '#FCCB00', '#008B02', '#006B76', '#1273DE', '#004DCF', '#5300EB']}
              defaultValue='#000000'
              color={this.state.darkColor}
              onChangeComplete={(color) => {
                this.setState({ darkColor: color.hex })
              }}
            />
          </FormControl>
          <div className={classes.margin} />
          <Button
            id='generate-button'
            variant='outlined'
            onClick={this.generateQRCode}
            className={classes.generateBtn}
          >
            Create Algorand QR Code
          </Button>
          <div className={classes.margin} />
          <div className={classes.qrcodeDiv}>
            <img className={classes.qrImage} src={this.state.dataURL} />
          </div>
          <div className={classes.textDiv}>
            <p className={classes.uri} id='algorand-uri-result'>
              {this.state.uri}
            </p>
          </div>
        </Paper>
      </div>
    )
  }
}
PaymentTransactionExample.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired
}
export default withStyles(styles)(PaymentTransactionExample)
