import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { ImageDataUrls } from 'algorand-qrcode-react'

const { logo } = ImageDataUrls()
const styles = (theme) => ({
  qrcodeDiv: {
    borderbottom: '10px solid rgb(115, 175, 245)',
    border: '1px solid #333',
    margin: 'auto',
    display: 'block'
    // padding: 10
  },
  qrImage: {
    width: '256px',
    height: '256px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      width: '140px',
      height: '140px'
    }
  },
  assetUrl: {
    [theme.breakpoints.down('xs')]: {
      wordWrap: 'break-word'
    }
  },
  textDiv: {
    backgroundColor: '#b460c3',
    borderColor: '#b460c3',
    margin: 'auto',
    display: 'block'
  },
  subTitle: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  uri: {
    padding: '10px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      fontSize: '0.7em',
      wordBreak: 'break-word'
    }
  },

})
class TransactionLabel extends Component {
  constructor() {
    super()
    this.state = {}
    
  }


  render() {
    const { classes,uri, dataURL} = this.props
    
    const {} = this.props;
    
    const self = this

    return (
      <div>
          <div className={classes.qrcodeDiv}>
            <img className={classes.qrImage} src={dataURL} />
          </div>
          <div className={classes.textDiv}>
            <p className={classes.uri} id='algorand-uri-result'>
              {uri}
            </p>
          </div>
        </div> 
    )
  }
}
TransactionLabel.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,
  dataURL: PropTypes.string.isRequired,
  uri: PropTypes.string.isRequired
}
export default withStyles(styles)(TransactionLabel)
