import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Apps from '@material-ui/icons/Apps'
import List from '@material-ui/icons/List'
import Collapse from '@material-ui/core/Collapse'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
// import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Avatar from '@material-ui/core/Avatar'
import { IconButton } from '@material-ui/core'
import nextId from 'react-id-generator'
import qrCodeSample from './ImageDataUrls'
import avatarSample from './ImageDataUrls'
const styles = (theme) => ({
  root: theme.mixins.gutters({
    // maxWidth: '90%',
    margin: 'auto',
    padding: theme.spacing(3),
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3)
  }),
  card: theme.mixins.gutters({
    margin: 'auto',
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3),
    border: '1px solid #333',
    padding: 0
  }),
  title: {
    margin: `${theme.spacing(3)}px 0 ${theme.spacing(2)}px`,
    color: theme.palette.protectedTitle,
    textAlign: 'center',
    fontSize: '1.2em'
  },
  tablePaperRoot: {
    display: 'flex',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'hide',
    width: '100%'
  },
  table: {
    minWidth: 340
  },
  tableCell: {
    paddingRight: 4,
    paddingLeft: 5
  },
  tableHeader: {
    paddingRight: 4,
    paddingLeft: 5,
    fontWeight: 'bold'
  },
  tableQrCodeImg: {
    width: 40,
    height: 40
  },
  avatar: {
    width: 80,
    height: 80,
    // marginLeft:'35%',
    marginBottom: '10%'
  },
  cardContent: {
    textAlign: 'center',
    alignItems: 'center'
  },
  qrCodeDiv: {
    border: '1px solid #333333',
    width: 100,
    height: 100,
    float: 'right',
    textAlign: 'center'
  },
  qrCodeImg: {
    width: 80,
    height: 80,
    marginTop: 10
  },
  dataDiv: {
    width: '100%'
  },
  dataTitle: {
    fontSize: '0.8em',
    fontWeight: 'bold',
    display: 'inline-block'
  },
  subTitle: {
    fontSize: '0.8em',
    display: 'inline-block'
  },
  collapseBtn: {
    fontSize: '0.7em',
    borderRadius: 50
  },
  classContentRoot: {
    minHeight: 200,
    padding: 0,
    paddingTop: 20
  }
})

function createData(
  id,
  title,
  avatar,
  description,
  price,
  url,
  creator,
  manager,
  clawback,
  freez,
  qrcode
) {
  return {
    id,
    title,
    avatar,
    description,
    price,
    url,
    creator,
    manager,
    clawback,
    freez,
    qrcode
  }
}

const rows = [
  createData(
    '423985',
    'Test',
    avatarSample,
    'This is a test desc',
    10,
    '',
    'This creator',
    'This manager',
    'fflowbback',
    'freeeeez',
    qrCodeSample
  ),
  createData(
    '5386915',
    'Second',
    avatarSample,
    'This is as.kfjla faghjasgfsfgsfg',
    20,
    'dfgagh',
    'gtaetawt rga',
    'fcbcbhzdzdg',
    'fftwe53aa',
    'gdfgsert',
    qrCodeSample
  ),
  createData(
    '63434967',
    'sdadasd',
    avatarSample,
    'asczx vfazsd fadk jga fgsdg sgfg ssn rgjhkgyiur te rtre;gh sc asczxd fadk jga fgsdg sgfg ssn rgjhkg vfazsd fadk jga fgsdg',
    15,
    'dfgvxdtgseagh',
    'dfgsdyjnv gngjctj',
    'sfaw5fbdfh',
    'dtsh',
    'erwerwer',
    qrCodeSample
  )
]

class SmartView extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isCardView: true,
      isTableView: false,
      expanded: false,
      index: ''
    }
    this.handleClickTableView = this.handleClickTableView.bind(this)
    this.handleClickCardView = this.handleClickCardView.bind(this)
    this.handleExpandClick = this.handleExpandClick.bind(this)
  }

  handleClickTableView() {
    this.setState({
      isTableView: true,
      isCardView: false
    })
  }

  handleClickCardView() {
    this.setState({
      isTableView: false,
      isCardView: true
    })
  }

  handleExpandClick(id) {
    this.setState({
      expanded: !this.state.expanded,
      index: id
    })
  }

  render() {
    const { classes, list } = this.props
    const { isCardView, isTableView, expanded, index } = this.state

    const cardView = () => (
      <Grid container spacing={2}>
        {list.map((row) => (
          <Grid key={nextId()} item xs={12} sm={6} md={3}>
            <Card className={classes.card}>
              <CardContent className={classes.classContentRoot}>
                <Grid container justifyContent='flex-end' alignItems='flex-end'>
                  <Grid item xs={6} sm={6} md={6}>
                    <Avatar className={classes.avatar} src={row.avatar} />
                  </Grid>
                  <Grid item xs={6} sm={6} md={6}>
                    <div className={classes.qrCodeDiv}>
                      <img src={row.qrcode} className={classes.qrCodeImg} />
                    </div>
                  </Grid>
                </Grid>
                <div
                  className={classes.dataDiv}
                  style={{ height: row.description.length < 100 && 158 }}
                >
                  <Typography className={classes.dataTitle}>ID:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.id}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Title:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.title}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>
                    Description:
                  </Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.description}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Price:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.price}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Url:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.url}
                  </Typography>
                </div>
              </CardContent>
              <CardActions disableSpacing>
                <Button
                  className={classes.collapseBtn}
                  onClick={() => this.handleExpandClick(row.id)}
                >
                  {index === row.id && expanded ? 'Less' : 'More'}
                </Button>
              </CardActions>
              <Collapse
                in={index === row.id && expanded}
                timeout='auto'
                unmountOnExit
              >
                <CardContent>
                  <Typography className={classes.dataTitle}>
                    Creator:
                  </Typography>
                  <Typography className={classes.subTitle}>
                    {row.creator}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>
                    Manager:
                  </Typography>
                  <Typography className={classes.subTitle}>
                    {row.manager}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>
                    Clawback:
                  </Typography>
                  <Typography className={classes.subTitle}>
                    {row.clowback}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Freez:</Typography>
                  <Typography className={classes.subTitle}>
                    {row.freez}
                  </Typography>
                  <br />
                </CardContent>
              </Collapse>
            </Card>
          </Grid>
        ))}
      </Grid>
    )

    const tableView = () => (
          <Table className={classes.table} size='small'>
            <TableHead>
              <TableRow>
                <TableCell align='left' className={classes.tableHeader}>
                  Id
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Avatar
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Qr code
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Title
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Description
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Price
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Url
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Creator
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Manager
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Clawback
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Freez
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {list.map((row) => (
                <TableRow key={row.id}>
                  <TableCell component='th' scope='row'>
                    {row.id}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    <Avatar className={classes.avatar} src={row.avatar} />
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    <img src={row.qrcode} className={classes.tableQrCodeImg} />
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.title}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.description}
                  </TableCell>
                  <TableCell align='center' className={classes.tableCell}>
                    {row.price}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.url}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.creator}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.manager}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.clawback}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.freez}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
    )
    return (
      <div>
          <IconButton onClick={this.handleClickCardView}>
            <Apps />
          </IconButton>
          <IconButton onClick={this.handleClickTableView}>
            <List />
          </IconButton>
          {isCardView && cardView()}
          {isTableView && tableView()}
      </div>
    )
  }
}
SmartView.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,
  list: PropTypes.array.isRequired
}
export default withStyles(styles)(SmartView)
