import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
//import Paper from '@material-ui/core/Paper'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Apps from '@material-ui/icons/Apps'
import List from '@material-ui/icons/List'
//import Collapse from '@material-ui/core/Collapse'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
// import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Avatar from '@material-ui/core/Avatar'
import { IconButton } from '@material-ui/core'
//import nextId from 'react-id-generator'
import logo from '../assets/images/favicon-32x32.png'
import {ImageDataUrls} from 'algorand-qrcode-react'

const { qrCodeSample, avatarSample } = ImageDataUrls()
const styles = (theme) => ({
  root: theme.mixins.gutters({
    // maxWidth: '90%',
    margin: 'auto',
    padding: theme.spacing(3),
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3)
  }),
  card: theme.mixins.gutters({
    margin: 'auto',
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3),
    border: '1px solid #333',
    padding: 0,
  }),
  title: {
    margin: `${theme.spacing(3)}px 0 ${theme.spacing(2)}px`,
    color: theme.palette.protectedTitle,
    textAlign: 'center',
    fontSize: '1.2em'
  },
  tablePaperRoot: {
    display: 'flex',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'hide',
    width: '100%'
  },
  table: {
    minWidth: 340
  },
  tableCell: {
    paddingRight: 4,
    paddingLeft: 5
  },
  tableHeader: {
    paddingRight: 4,
    paddingLeft: 5,
    fontWeight: 'bold'
  },
  tableQrCodeImg: {
    width: 40,
    height: 40
  },
  avatar: {
    width: 80,
    height: 80,
    // marginLeft:'35%',
    marginBottom: '10%'
  },
  cardContent: {
    textAlign: 'left',
  },
  qrCodeDiv: {
    border: '1px solid #333333',
    width: 100,
    height: 100,
    float: 'right',
    textAlign: 'center'
  },
  qrCodeImg: {
    width: 80,
    height: 80,
    marginTop: 10
  },
  dataDiv: {
    width: '100%',
    textAlign: 'left'
  },
  dataTitle: {
    fontSize: '0.8em',
    fontWeight: 'bold',
    display: 'inline'
  },
  subTitle: {
    fontSize: '0.7em',
    display: 'inline',
    wordBreak: 'break-word'
  },
  collapseBtn: {
    fontSize: '0.8em',
    borderRadius: 50,
    fontWeight: 'bold' 
  },
  classContentRoot: {
    minHeight: 200,
    padding: 0,
    paddingTop: 20
  },
  classContentRootCurrentAccount: {
    backgroundColor: 'lightgreen',
    minHeight: 200,
    padding: 0,
    paddingTop: 20
  },
  
})

class AccountsSmartView extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isCardView: true,
      isTableView: false,
      expanded: false,
      index: ''
    }
    this.handleClickTableView = this.handleClickTableView.bind(this)
    this.handleClickCardView = this.handleClickCardView.bind(this)
    this.handleExpandClick = this.handleExpandClick.bind(this)
  }

  handleClickTableView() {
    this.setState({
      isTableView: true,
      isCardView: false
    })
  }

  handleClickCardView() {
    this.setState({
      isTableView: false,
      isCardView: true
    })
  }

  handleExpandClick(id) {
    this.setState({
      expanded: !this.state.expanded,
      index: id
    })
  }

  render() {
    const { classes, list } = this.props
    const { isCardView, isTableView, expanded, index } = this.state

    const cardView = () => (
      <Grid container spacing={2}>
        {list.map((row) => (
          <Grid key={row.id} item xs={12} sm={6} md={3} style={{display: 'flex'}}>
            <Card className={classes.card}>
              <CardContent className={row.currentAccount ? classes.classContentRoot : classes.classContentRootCurrentAccount}>
                <Grid container justifyContent='flex-end' alignItems='flex-end'>
                  <Grid item xs={6} sm={6} md={6}>
                    <Avatar className={classes.avatar} src={logo} />
                  </Grid>
                  <Grid item xs={6} sm={6} md={6}>
                    <div className={classes.qrCodeDiv}>
                      <img src={row.qrcode ? row.qrcode : qrCodeSample} className={classes.qrCodeImg} />
                    </div>
                  </Grid>
                </Grid>
                <div
                  className={classes.dataDiv}
                  // style={{ height: row.description.length < 100 && 158 }}
                >
                  <Typography className={classes.dataTitle}>#</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.id}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Account:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.title}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>Asset:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.asset}
                  </Typography>
                  <br />
                  <Typography className={classes.dataTitle}>URL:</Typography>
                  <Typography className={classes.subTitle}>
                    {' '}
                    {row.url}
                  </Typography>
                </div>
              </CardContent>
              <CardActions>
                <Button
                  className={classes.collapseBtn}                  
                >
                <a target="_blank" href={`${row.url}`}>
                  {'Check on AlgoExplorer'}
                </a>
                 
                </Button>
              </CardActions>
                </Card>
          </Grid>
        ))}
      </Grid>
    )

    const tableView = () => (
          <Table className={classes.table} size='small'>
            <TableHead>
              <TableRow>
                <TableCell align='left' className={classes.tableHeader}>
                  #
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Account Address
                </TableCell>
                <TableCell align='left' className={classes.tableHeader}>
                  Asset ID
                </TableCell> 
                 
                        
                <TableCell align='left' className={classes.tableHeader}>
                  QR Code
                </TableCell>
                           
                <TableCell align='left' className={classes.tableHeader}>
                  URL
                </TableCell>
                
              </TableRow>
            </TableHead>
            <TableBody>
              {list.map((row) => (
                <TableRow key={row.id}>
                  <TableCell component='th' scope='row'>
                    {row.id}
                  </TableCell>
                  <TableCell align='left' className={classes.tableCell}>
                    {row.title}
                  </TableCell>
                  
                  <TableCell align='left' className={classes.tableCell}>
                    {row.asset}
                  </TableCell> 
                   
                  <TableCell align='left' className={classes.tableCell}>
                    <img src={row.qrcode ? row.qrcode : qrCodeSample} className={classes.tableQrCodeImg} />
                  </TableCell>          
                  <TableCell align='left' className={classes.tableCell}>
                  <a target="_blank" href={row.url}>
                  {row.url}
                </a>
                  </TableCell>           
                </TableRow>
              ))}
            </TableBody>
          </Table>
    )
    return (
      <div>
          <IconButton onClick={this.handleClickCardView}>
            <Apps />
          </IconButton>
          <IconButton onClick={this.handleClickTableView}>
            <List />
          </IconButton>
          {isCardView && cardView()}
          {isTableView && tableView()}
      </div>
    )
  }
}
AccountsSmartView.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired,
  list: PropTypes.array.isRequired
}
export default withStyles(styles)(AccountsSmartView)
