import React from "react";
import PropTypes from 'prop-types'
// @material-ui/core components
import { withStyles } from "@material-ui/core/styles";
// core components
import GridItem from "../../components/Grid/GridItem.js";
import GridContainer from "../../components/Grid/GridContainer.js";
import Card from "../../components/Card/Card.js";
import CardAvatar from "../../components/Card/CardAvatar.js";
import CardBody from "../../components/Card/CardBody.js";
import CustomTabs from "../../components/CustomTabs/CustomTabs.js";
import TransactionForm from "../../components/TransactionComponent/TransactionForm";
import TransactionLabel from "../../components/TransactionComponent/TransactionLabel";
import avatar from "../../assets/images/qrlogo.svg";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import SecondDeviceScanner from '../../components/SecondDevice/SecondDeviceScanner'
import SecondDeviceResults from '../../components/SecondDevice/SecondDeviceResults'
import { ImageDataUrls } from 'algorand-qrcode-react'
const { logo } = ImageDataUrls()

const styles = {
  cardAvatar:{
    backgroundColor: '#fff'
  }
};

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#b460c3',
        opacity: 1,
        border: 'none',
      },
    },
    '&$focusVisible $thumb': {
      color: '#b460c3',
      border: '6px solid #fff',
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border']),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});


class TransactionsExample extends React.Component {
  constructor() {
    super()
    this.state = {
      uri: 'algorand://',
      dataURL: logo,
      isSecondDeviceActive: false
    }
    this.handleChangeSwitch= this.handleChangeSwitch.bind(this)
    
  }
  handleChangeSwitch(){
    this.setState({
      isSecondDeviceActive: !this.state.isSecondDeviceActive
    })
  }
  render(){
    const {classes} = this.props
    const {isSecondDeviceActive, uri, dataURL} = this.state
    const self = this
    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={8}>
            <CustomTabs
              headerColor="primary"
              tabs={[
                {
                  tabName: "Payment Trx",
                  tabContent: (
                    isSecondDeviceActive ? <SecondDeviceResults /> :<TransactionForm type='payment' printGeneratedQrcode={(uri, dataURL)=>self.setState({uri: uri, dataURL: dataURL})} />
                  ),
                },
                {
                  tabName: "Offline Trx",
                  tabContent: (
                    isSecondDeviceActive ? <SecondDeviceResults /> :<TransactionForm type='offline' printGeneratedQrcode={(uri, dataURL)=>self.setState({uri: uri, dataURL: dataURL})} />                    
                  ),
                },
                {
                  tabName: "Group Trx",
                  tabContent: (
                    isSecondDeviceActive ? <SecondDeviceResults /> :<TransactionForm type='group' printGeneratedQrcode={(uri, dataURL)=>self.setState({uri: uri, dataURL: dataURL})} />                    

                  ),
                },
              ]}
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <Card profile>
              <CardAvatar profile className={classes.cardAvatar}>
                <a href="#" onClick={(e) => e.preventDefault()}>
                  <img src={logo} alt="logo" />
                </a>
              </CardAvatar>
              <CardBody profile>
              <FormControlLabel
                control={<IOSSwitch checked={isSecondDeviceActive} onChange={this.handleChangeSwitch} name="isSecondDevice" />}
                label="Second device"
              />
                
              {isSecondDeviceActive ? <SecondDeviceScanner />  : <TransactionLabel uri={uri} dataURL={dataURL}/>}
              </CardBody>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

TransactionsExample.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired
}

export default withStyles(styles)(TransactionsExample);
